<!-- 超级管理员--评分看板 -->
<script setup>
import { reactive, ref, onMounted, onUnmounted } from "vue";
import { FormOutlined } from "@ant-design/icons-vue";
import { message } from "ant-design-vue";
import { getDepartmentListApi } from "@/api/userManage";
import { getRateDataApi, getRateDetailApi, getEditConfidenceApi, getRejectScoreApi, getConfirmsSingleScoreApi, getConfirmsManyScoreApi, getPushScoreApi } from "@/api/rateBoard";
import { getStepTimeApi } from "@/api/scoreTimeSet"

// 获取用户角色
const userRole = ref(localStorage.getItem('userRole'));
const isVisible = ref(false)
if (userRole.value === '3') {
    isVisible.value = true
}

// 获取当前评估环节
const isRateBoard = ref(true);
const currentSession = ref()
// 获得当前评估进度
const getStepTime = () => {
    getStepTimeApi()
        .then(res => {
            if (res.data.code === 200) {
                for (let i = 0; i <= res.data.data.evaluateStep.length - 1; i++) {
                    if (res.data.data.evaluateStep[i].enable === 1) {
                        currentSession.value = i + 1 + '';
                        if (currentSession.value !== '2') {
                            isRateBoard.value = false
                        }
                    }
                }
            } else {
                message.error('获取失败！'+ res.data.msg);
            }
        })
        .catch(err => {
            console.log('获取失败！', err);
        });
};


// 搜索框表单数据
const serchInfo = reactive({
    name: null,
    department: null,
    status: null,
});

// 分页配置
const pagination = reactive({
    current: 1,
    pageSize: 15,
    total: 0,
    showTotal: (total, range) => `总共 ${total} 条数据`, // 在这里定义显示总数据量的格式
});

// 部门列表
const departmentList = ref([]);

// 获取部门列表
const fetchDepartmentList = () => {
    getDepartmentListApi().then((res) => {
        if (res.data.code === 200) {
            departmentList.value = res.data.data;
        } else {
            message.error("获取部门列表失败,"+res.data.msg);
        }
    }).catch((err) => {
        message.error("请求失败", err);
    });
};

// 搜索表单提交
const searchRatingList = () => {
    pagination.current = 1;
    getRateTableData();
};

// 表格列配置
const columns = ref([
    {
        title: '序号',
        dataIndex: 'id',
        key: 'id',
        fixed: 'left',  // 固定在左侧
        align: 'center',
        width: 65,
    },
    {
        title: '评估人',
        dataIndex: 'Evaluator',
        key: 'Evaluator',
        fixed: 'left',  // 固定在左侧
        align: 'center',
        width: 90,
    },
    {
        title: '部门',
        dataIndex: 'department',
        key: 'department',
        align: 'center',
        width: 90,
    },
    {
        title: '状态',
        dataIndex: 'status',
        key: 'status',
        align: 'center',
        width: 80,
        slots: { customRender: 'status' }

    },
    {
        title: '置信度',
        dataIndex: 'confidence',
        key: 'confidence',
        align: 'center',
        width: 75,
    },
    {
        title: '开放进取 (O)',
        dataIndex: 'openProgressive',
        key: 'openProgressive',
        align: 'center',
        width: 100,
    },
    {
        title: '打胜仗作为信仰 (W)',
        dataIndex: 'winBelief',
        key: 'winBelief',
        align: 'center',
        width: 110,
    },
    {
        title: '创新求实 (I)',
        dataIndex: 'innovationRealism',
        key: 'innovationRealism',
        align: 'center',
        width: 100,
    },
    {
        title: '信任互助 (H)',
        dataIndex: 'trustHelp',
        key: 'trustHelp',
        align: 'center',
        width: 100,
    },
    {
        title: '尊重平等 (R)',
        dataIndex: 'respectEquality',
        key: 'respectEquality',
        align: 'center',
        width: 100,
    },
    {
        title: '业精于勤 (D)',
        dataIndex: 'diligenceExcellence',
        key: 'diligenceExcellence',
        align: 'center',
        width: 100,
    },
    {
        title: '使命必达 (M)',
        dataIndex: 'missionAccomplished',
        key: 'missionAccomplished',
        align: 'center',
        width: 100,
    },
    {
        title: '五湖四海 (F)',
        dataIndex: 'globalView',
        key: 'globalView',
        align: 'center',
        width: 100,
    },
    {
        title: '坚持自省 (I)',
        dataIndex: 'selfReflection',
        key: 'selfReflection',
        align: 'center',
        width: 100,
    },
    {
        title: '操作',
        key: 'action',
        fixed: 'right',  // 固定在右侧
        align: 'center',
        width: 260,
        slots: { customRender: 'action' }
    },
])
// 表格数据
const tableData = ref([]);
const scrollHeight = ref('60vh');  // 定义滚动区域高度

// 动态更新高度
const updateScrollHeight = () => {
    const screenHeight = window.innerHeight;
    const containerHeight = screenHeight - 50 - 10; // 假设父容器需要减去固定的偏移量
    if (screenHeight < 800) {
        scrollHeight.value = `${containerHeight * 0.6}px`; // 小屏幕高度为 50%
    } else {
        scrollHeight.value = `${containerHeight * 0.67}px`; // 大屏幕高度为 80%
    }
};
// 调用api-获取表格数据
const getRateTableData = () => {
    tableData.value = []
    getRateDataApi(pagination.current, pagination.pageSize, serchInfo.name, serchInfo.department, serchInfo.status).then(res => {
        if (res.data.code === 200) {
            // 设置分页总数
            pagination.total = res.data.data.total;
            const result = res.data.data.data
            for (let i = 0; i < result.length; i++) {
                tableData.value.push(
                    {
                        id: pagination.current * pagination.pageSize - pagination.pageSize + i + 1,
                        userId: result[i].userId,
                        Evaluator: result[i].evaluatorName,
                        department: result[i].department,
                        openProgressive: result[i].scoreList[0],
                        winBelief: result[i].scoreList[1],
                        innovationRealism: result[i].scoreList[2],
                        trustHelp: result[i].scoreList[3],
                        respectEquality: result[i].scoreList[4],
                        diligenceExcellence: result[i].scoreList[5],
                        missionAccomplished: result[i].scoreList[6],
                        globalView: result[i].scoreList[7],
                        selfReflection: result[i].scoreList[8],
                        confidence: result[i].confidenceLevel,
                        status: result[i].state
                    }
                )
            }
        } else {
            message.error('获取评分列表失败,'+res.data.msg)
        }
    }).catch(err => {
        message.error('请求失败！', err)
    })
}
// 表格变化处理
const handleTableChange = (e) => {
    pagination.current = e.current;
    getRateTableData();
};

// 多选框配置
const state = reactive({
  selectedRowKeys: [],
});
const onSelectChange = selectedRowKeys => {
    state.selectedRowKeys = selectedRowKeys;
};

// 查看详情
const viewDetail = (record) => {
    // 获取当前评估人信息
    evaluatorNow.value = record.Evaluator
    evaluatorDepartmentNow.value = record.department
    // 调用接口获取当前评估人的打分详情
    getScoreDetailsData(record.userId)
    // 打开弹窗
    openTable.value = true
};

// 驳回评分
const rejectRating = (record) => {
    if (record.status === '已确认') {
        message.warning("操作失败！您已确认" + record.Evaluator + "的打分，不可再驳回~");
        return;
    }
    getRejectScoreApi(record.userId).then(res => {
        if (res.data.code === 200) {
            message.success("已驳回" + record.Evaluator + "的评分!");
            getRateTableData()
        } else {
            message.error("驳回失败" + res.data.msg);
        }
    }).catch(err => {
        message.error('请求失败！', err)
    })
};

// 确认评分
const confirmRating = (record) => {
    getConfirmsSingleScoreApi(record.userId).then(res => {
        if (res.data.code === 200) {
            message.success("已确认" + record.Evaluator + "的评分! ^_^");
            getRateTableData()
        } else {
            message.error("确认失败，" + res.data.msg);
        }
    }).catch(err => {
        message.error('请求失败！', err)
    })
};

// 批量确认
const handleManyConfirm = () => {
    // 在确认之前判断是否有选择项
    if (state.selectedRowKeys.length === 0) {
        message.warning("请先选择评估人！");
        return;
    }

    getConfirmsManyScoreApi(state.selectedRowKeys).then(res => {
        if (res.data.code === 200) {
            // 显示成功消息
            message.success(`已确认${state.selectedRowKeys.length}位评估人的评分! ^_^`);

            // 清空选择项
            state.selectedRowKeys = [];

            // 重新获取评分表格数据
            getRateTableData()
        } else {
            message.error("操作失败，" + res.data.msg);
        }
    }).catch(err => {
        message.error('请求失败！'+ err)
    })
};

const openTable = ref(false)
const dataScoreDetails = ref([])
const evaluatorNow = ref('')
const evaluatorDepartmentNow = ref('')
// 打分详情表格页表头配置
const columnsScoreDetails = ref([
    {
        title: '被评估人',
        dataIndex: 'assessor',
        key: 'assessor',
        fixed: 'left',  // 固定在左侧
        align: 'center',
        width: 90,
    },
    {
        title: '开放进取 (O)',
        dataIndex: 'openProgressive',
        key: 'openProgressive',
        align: 'center',
        // width: 100,
    },
    {
        title: '打胜仗作为信仰 (W)',
        dataIndex: 'winBelief',
        key: 'winBelief',
        align: 'center',
        // width: 120,
    },
    {
        title: '创新求实 (I)',
        dataIndex: 'innovationRealism',
        key: 'innovationRealism',
        align: 'center',
        // width: 100,
    },
    {
        title: '信任互助 (H)',
        dataIndex: 'trustHelp',
        key: 'trustHelp',
        align: 'center',
        // width: 100,
    },
    {
        title: '尊重平等 (R)',
        dataIndex: 'respectEquality',
        key: 'respectEquality',
        align: 'center',
        // width: 100,
    },
    {
        title: '业精于勤 (D)',
        dataIndex: 'diligenceExcellence',
        key: 'diligenceExcellence',
        align: 'center',
        // width: 100,
    },
    {
        title: '使命必达 (M)',
        dataIndex: 'missionAccomplished',
        key: 'missionAccomplished',
        align: 'center',
        // width: 100,
    },
    {
        title: '五湖四海 (F)',
        dataIndex: 'globalView',
        key: 'globalView',
        align: 'center',
        // width: 100,
    },
    {
        title: '坚持自省 (I)',
        dataIndex: 'selfReflection',
        key: 'selfReflection',
        align: 'center',
        // width: 100,
    },
    {
        title: '总分',
        dataIndex: 'totalScore',
        key: 'totalScore',
        align: 'center',
        width: 80,
    }
])
// 调用api-获取表格数据
const getScoreDetailsData = (id) => {
    dataScoreDetails.value = []
    getRateDetailApi(id).then(res => {
        if (res.data.code === 200) {
            const result = res.data.data
            for (let i = 0; i < result.length; i++) {
                dataScoreDetails.value.push(
                    {
                        id: i,
                        assessor: result[i].evaluatedName,
                        openProgressive: result[i].scores[0],
                        winBelief: result[i].scores[1],
                        innovationRealism: result[i].scores[2],
                        trustHelp: result[i].scores[3],
                        respectEquality: result[i].scores[4],
                        diligenceExcellence: result[i].scores[5],
                        missionAccomplished: result[i].scores[6],
                        globalView: result[i].scores[7],
                        selfReflection: result[i].scores[8],
                        totalScore: result[i].allScore
                    }
                )
            }
        }else{
            message.error('获取详情数据失败，' + res.data.msg)
        }
    }).catch(err => {
        message.error('请求失败！', err)
    })
}
// 关闭弹窗函数
const closeModal = e => {
    openTable.value = false;
};

// 弹窗相关的状态
const isConfidenceModalOpen = ref(false);
// 置信度表单数据
const confidenceForm = ref({
    confidence: '', // 默认置信度值
});
// 设置置信度
const confidenceUserId = ref('')
const setConfidence = (record) => {
    confidenceForm.value.confidence = record.confidence; // 回显当前记录的置信度
    confidenceUserId.value = record.userId
    isConfidenceModalOpen.value = true; // 显示弹窗
};
// 提交置信度
const handleConfidenceSubmit = () => {
    let confidenceValue = confidenceForm.value.confidence;

    // 检查置信度是否为空
    if (confidenceValue === null || confidenceValue === '') {
        message.error('请填写置信度！');
        return; // 退出函数
    }

    // 检查置信度是否是有效的数字
    if (isNaN(confidenceValue)) {
        message.error('置信度必须是一个有效的数字！');
        return; // 退出函数
    }

    // 检查置信度是否在合法范围 [0, 1] 内
    if (confidenceValue < 0 || confidenceValue > 1) {
        message.error('置信度必须在 0 到 1 之间！');
        return; // 退出函数
    }

    getEditConfidenceApi(confidenceUserId.value, confidenceForm.value.confidence).then(res => {
        if (res.data.code === 200) {
            // 提示设置成功
            message.success("设置成功！")
            // 关闭弹窗
            isConfidenceModalOpen.value = false;
            // 刷新表格
            getRateTableData()
        } else {
            message.error("设置失败！", res.data.msg)
        }
    }).catch(err => {
        message.error('请求失败！', err)
    })
};

// 取消弹窗
const handleConfidenceModalCancel = () => {
    isConfidenceModalOpen.value = false; // 关闭弹窗
};

const handlNextStep = () => {
    getPushScoreApi().then(res => {
        if (res.data.code === 200) {
            message.success("操作成功!")
            isRateBoard.value = false
            getRateTableData()
        } else {
            message.error("操作失败！" + res.data.msg)
        }
    }).catch(err => {
        message.error('请求失败！', err)
    })
}

// 初始加载数据
onMounted(() => {
    if (localStorage.getItem('userRole') === '3') {
        fetchDepartmentList();
        getRateTableData();
        getStepTime();
    }

    updateScrollHeight();
    window.addEventListener('resize', updateScrollHeight);
});

// 在组件卸载时移除事件监听
onUnmounted(() => {
    window.removeEventListener('resize', updateScrollHeight);
});
</script>

<template>
    <div v-if="isVisible" class="rate-board">
        <!-- 表格顶部区域 -->
        <div class="content-header">
            <!-- 搜索表单 -->
            <div class="search-form">
                <a-form style="display: flex; align-items: center;">
                    <a-form-item label="评估人" style="margin-right: 20px; width: 200px">
                        <a-input placeholder="请输入姓名" v-model:value="serchInfo.name" :allowClear="true" />
                    </a-form-item>
                    <a-form-item label="部门" style="margin-right: 20px; width: 200px">
                        <a-select placeholder="请选择部门" v-model:value="serchInfo.department" :allowClear="true">
                            <a-select-option v-for="item in departmentList" :key="item" :value="item">
                                {{ item }}
                            </a-select-option>
                        </a-select>
                    </a-form-item>
                    <a-form-item label="状态" style="margin-right: 20px; width: 200px">
                        <a-select placeholder="请选择状态" v-model:value="serchInfo.status" :allowClear="true">
                            <a-select-option value="">全部</a-select-option>
                            <a-select-option value="待确认">待确认</a-select-option>
                            <a-select-option value="已确认">已确认</a-select-option>
                        </a-select>
                    </a-form-item>
                    <a-form-item>
                        <a-button type="primary" @click.prevent="searchRatingList" class="searchBtn">搜索</a-button>
                    </a-form-item>
                </a-form>
            </div>
            <!-- 右侧按钮 -->
            <div class="submit-Btn">
                <a-button type="primary" @click="handleManyConfirm" style="margin-right: 10px;">
                    <template #icon>
                        <FormOutlined />
                    </template>
                    批量确认
                </a-button>
                <a-popconfirm placement="bottomRight"
                    title="请再次检查是否所有成员的评分都已确认，若有未确认的评分，直接进入下一步后，将自动确认所有评分哦！进入下一环节后还需在<得分看板>将结果设置可见" ok-text="确定"
                    cancel-text="取消" @confirm="handlNextStep()">
                    <a-button v-if="isRateBoard" type="primary" style="background-color: #68BBC4;">
                        进入下一环节
                    </a-button>
                </a-popconfirm>
                <a-button v-if="!isRateBoard" type="primary" disabled>
                    不在当前环节
                </a-button>
            </div>
        </div>

        <!-- 表格内容区域 -->
        <div class="content-table">
            <a-table :columns="columns" :data-source="tableData" :pagination="pagination" :row-selection="{ selectedRowKeys: state.selectedRowKeys, onChange: onSelectChange }"
                :bordered="true" :scroll="{ y: scrollHeight }" @change="handleTableChange" row-key="userId">

                <!-- 自定义空数据时的显示 -->
                <template #emptyText>
                </template>

                <!-- 状态列 -->
                <template #status="{ text }">
                    <a-tag color="green" v-if="text === '已确认'" style="margin-right: 0px;">已确认</a-tag>
                    <a-tag color="red" v-if="text === '待确认'" style="margin-right: 0px;">待确认</a-tag>
                </template>

                <!-- 操作栏 -->
                <template #action="{ record }">
                    <div class="action-btn">
                        <span @click="viewDetail(record)">查看详情</span>
                        <span @click="setConfidence(record)">设置置信度</span>
                        <a-popconfirm title="确定要驳回吗?" ok-text="确定" cancel-text="取消" @confirm="rejectRating(record)"><span>驳回</span>
                        </a-popconfirm>
                        <span @click="confirmRating(record)">确认</span>
                    </div>
                </template>
            </a-table>
        </div>

        <!-- 查看详情弹窗 -->
        <a-modal v-model:open="openTable" title="查看详情" @ok="closeModal" style="width: 90vw;" class="score-table">
            <span style="padding: 5px 5px 0px 5px;">
                <span style="color: #3894FF;">{{ evaluatorNow }}（{{ evaluatorDepartmentNow }}）的打分详情</span>
            </span>
            <a-table :columns="columnsScoreDetails" :data-source="dataScoreDetails" :pagination="false"
                bordered></a-table>
        </a-modal>

        <!-- 设置置信度弹窗 -->
        <a-modal v-model:open="isConfidenceModalOpen" title="设置置信度" @ok="handleConfidenceSubmit"
            @cancel="handleConfidenceModalCancel">
            <a-input-number v-model:value="confidenceForm.confidence" style="width: 100%" />
        </a-modal>
    </div>
    <div v-else class="no-permission">
        <a-empty description="您暂无权限查看该页面0~0" />
    </div>
</template>

<style lang="less" scoped>
